<template>
  <div>
    <div class="title">热销推荐</div>
    <ul>
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail/' + item.id"
      >
			
        <img class="item-img" :src="item.imgUrl" />
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>

<style lang="scss" scoped>
	@import '~@/styles/mixins.scss';
  .title{
  	margin-top: .2rem;
  	line-height: .8rem;
  	background: #eee;
  	text-indent: .2rem;
	}
	.item{
		height: 1.9rem;
		display: flex;
		overflow: hidden;
		.item-img{
			width:1.7rem;
			heihgt:1.7rem;
			padding:.1rem;
		}
		.item-info{
			flex: 1;
			min-width: 0;
			padding:.1rem;
			.item-title{
				line-height: 0.54rem;
				font-size: 0.32rem;
			}
			.item-desc{
				line-height: .44rem;
				color:#313131;
				@include ellipsis();
			}
			.item-button{
				line-height: 0.5rem;
				margin-top:.2rem;
				background: #ff9300;
				padding:0 0.2rem;
				border-radius: 0.06rem;
				color:#fff;
			}
		}
	}
</style>



